"use client"
import React, { useState, useMemo } from "react"
import { PlusIcon, PencilIcon, TrashIcon, ChevronUpIcon, ChevronDownIcon, StarIcon as StarIconSolid } from "@heroicons/react/24/solid"
import { StarIcon as StarIconOutline, MagnifyingGlassIcon } from "@heroicons/react/24/outline"

// --- TYPES AND MOCK DATA ---

type Service = {
  id: string
  image: string
  name: string
  category: 'Beauty' | 'Wellness' | 'Fitness' | 'Medical'
  description: string
  price: number
  duration: number // in minutes
  providerCount: number
  rating: number
  bookings: number
  revenue: number
  status: 'Active' | 'Inactive'
  featured: boolean
  createdAt: string // ISO date string
}

const mockServices: Service[] = [
  { id: 'SRV001', image: 'https://placehold.co/80x80/E2E8F0/4A5568?text=Service1', name: 'Deep Tissue Massage', category: 'Wellness', description: 'A therapeutic massage using firm pressure and slow strokes to reach deeper layers of muscle.', price: 90.00, duration: 60, providerCount: 5, rating: 4.8, bookings: 120, revenue: 10800, status: 'Active', featured: true, createdAt: '2023-01-10T10:00:00Z' },
  { id: 'SRV002', image: 'https://placehold.co/80x80/E2E8F0/4A5568?text=Service2', name: 'Yoga Class', category: 'Fitness', description: 'A vinyasa flow class for all levels, focusing on breath and movement.', price: 25.00, duration: 75, providerCount: 3, rating: 4.9, bookings: 350, revenue: 8750, status: 'Active', featured: false, createdAt: '2023-02-15T11:30:00Z' },
  { id: 'SRV003', image: 'https://placehold.co/80x80/E2E8F0/4A5568?text=Service3', name: 'Luxury Manicure', category: 'Beauty', description: 'Includes shaping, cuticle care, a moisturizing massage, and polish.', price: 45.00, duration: 45, providerCount: 8, rating: 4.7, bookings: 210, revenue: 9450, status: 'Inactive', featured: true, createdAt: '2023-03-20T09:00:00Z' },
  { id: 'SRV004', image: 'https://placehold.co/80x80/E2E8F0/4A5568?text=Service4', name: 'Dental Check-up', category: 'Medical', description: 'A comprehensive dental examination, cleaning, and consultation.', price: 150.00, duration: 30, providerCount: 12, rating: 4.9, bookings: 450, revenue: 67500, status: 'Active', featured: false, createdAt: '2022-11-05T14:00:00Z' },
  { id: 'SRV005', image: 'https://placehold.co/80x80/E2E8F0/4A5568?text=Service5', name: 'Personal Training Session', category: 'Fitness', description: 'A one-on-one session with a certified personal trainer to help you reach your fitness goals.', price: 75.00, duration: 60, providerCount: 10, rating: 4.9, bookings: 180, revenue: 13500, status: 'Active', featured: true, createdAt: '2023-05-01T08:00:00Z' },
];


// --- HELPER COMPONENTS ---

const CategoryBadge = ({ category }: { category: Service['category'] }) => {
  const styles = {
    Beauty: 'bg-pink-100 text-pink-800',
    Wellness: 'bg-blue-100 text-blue-800',
    Fitness: 'bg-green-100 text-green-800',
    Medical: 'bg-red-100 text-red-800',
  }
  return <span className={`px-2 py-1 rounded-full text-xs font-medium ${styles[category]}`}>{category}</span>
}

const Rating = ({ rating }: { rating: number }) => (
    <div className="flex items-center">
      {[...Array(5)].map((_, i) => (
        i < Math.round(rating) ? <StarIconSolid key={i} className="h-4 w-4 text-yellow-400" /> : <StarIconOutline key={i} className="h-4 w-4 text-gray-300" />
      ))}
      <span className="ml-1 text-xs text-gray-600">({rating.toFixed(1)})</span>
    </div>
);
  
const ToggleSwitch = ({ enabled, onChange }: { enabled: boolean, onChange: (enabled: boolean) => void }) => (
    <div 
        onClick={() => onChange(!enabled)}
        className={`relative inline-flex items-center h-6 rounded-full w-11 cursor-pointer transition-colors duration-300 ease-in-out ${enabled ? 'bg-green-500' : 'bg-gray-300'}`}
    >
        <span className={`inline-block w-4 h-4 transform bg-white rounded-full transition-transform duration-300 ease-in-out ${enabled ? 'translate-x-6' : 'translate-x-1'}`} />
    </div>
);


// --- MAIN PAGE COMPONENT ---

export default function ServicesPage() {
  const [services, setServices] = useState(mockServices)
  const [searchTerm, setSearchTerm] = useState("")
  const [sortConfig, setSortConfig] = useState<{ key: keyof Service; direction: 'asc' | 'desc' } | null>(null);

  const filteredAndSortedServices = useMemo(() => {
    let sortedServices = [...services];

    if (sortConfig !== null) {
      sortedServices.sort((a, b) => {
        if (a[sortConfig.key] < b[sortConfig.key]) {
          return sortConfig.direction === 'asc' ? -1 : 1;
        }
        if (a[sortConfig.key] > b[sortConfig.key]) {
          return sortConfig.direction === 'asc' ? 1 : -1;
        }
        return 0;
      });
    }

    return sortedServices.filter(service =>
      service.name.toLowerCase().includes(searchTerm.toLowerCase())
    );
  }, [services, searchTerm, sortConfig]);

  const requestSort = (key: keyof Service) => {
    let direction: 'asc' | 'desc' = 'asc';
    if (sortConfig && sortConfig.key === key && sortConfig.direction === 'asc') {
      direction = 'desc';
    }
    setSortConfig({ key, direction });
  };
  
  const handleStatusChange = (serviceId: string, newStatus: 'Active' | 'Inactive') => {
    setServices(currentServices =>
      currentServices.map(s => s.id === serviceId ? { ...s, status: newStatus } : s)
    );
  };

  const serviceColumns = [
    { key: 'id', label: 'Service ID', width: '100px' },
    { key: 'image', label: 'Image', width: '80px' },
    { key: 'name', label: 'Service Name', sortable: true, searchable: true },
    { key: 'category', label: 'Category', filterable: true },
    { key: 'description', label: 'Description', truncate: 100 },
    { key: 'price', label: 'Price', sortable: true },
    { key: 'duration', label: 'Duration' },
    { key: 'providerCount', label: 'Providers', sortable: true },
    { key: 'rating', label: 'Rating', sortable: true },
    { key: 'bookings', label: 'Total Bookings', sortable: true },
    { key: 'revenue', label: 'Revenue', sortable: true },
    { key: 'status', label: 'Status', filterable: true },
    { key: 'featured', label: 'Featured' },
    { key: 'createdAt', label: 'Created', sortable: true },
    { key: 'actions', label: 'Actions' }
  ];

  const renderCell = (service: Service, key: string) => {
    const cellValue = service[key as keyof Service];

    switch (key) {
        case 'image':
            return <img className="h-12 w-12 rounded-md object-cover" src={service.image} alt={service.name} />;
        case 'name':
            return <div className="font-medium text-gray-900">{service.name}</div>;
        case 'category':
            return <CategoryBadge category={service.category} />;
        case 'description':
            return <p className="text-sm text-gray-600 truncate max-w-xs">{service.description}</p>;
        case 'price':
            return <span className="text-sm text-gray-800">${(cellValue as number).toFixed(2)}</span>;
        case 'duration':
            return <span className="text-sm text-gray-600">{cellValue} min</span>;
        case 'providerCount':
            return <span className="text-sm text-center text-gray-600">{cellValue}</span>;
        case 'rating':
            return <Rating rating={cellValue as number} />;
        case 'bookings':
        case 'revenue':
            return <span className="text-sm text-gray-800 text-right">{key === 'revenue' ? '$' : ''}{ (cellValue as number).toLocaleString() }</span>;
        case 'status':
            return <ToggleSwitch enabled={cellValue === 'Active'} onChange={(newEnabled) => handleStatusChange(service.id, newEnabled ? 'Active' : 'Inactive')} />;
        case 'featured':
            return <div className="flex justify-center">{cellValue ? <StarIconSolid className="h-5 w-5 text-yellow-500" /> : <StarIconOutline className="h-5 w-5 text-gray-300" />}</div>;
        case 'createdAt':
            return <span className="text-sm text-gray-600">{new Date(cellValue as string).toLocaleDateString()}</span>;
        case 'actions':
            return (
                <div className="flex items-center space-x-3">
                    <button className="text-blue-600 hover:text-blue-900 transition-colors"><PencilIcon className="h-5 w-5" /></button>
                    <button className="text-red-600 hover:text-red-900 transition-colors"><TrashIcon className="h-5 w-5" /></button>
                </div>
            );
        default:
            return <span className="text-sm text-gray-600">{String(cellValue)}</span>;
    }
  };
  
  return (
    <div className="container mx-auto px-4 py-8 bg-gray-50 min-h-screen">
      <div className="flex justify-between items-center mb-8">
        <h1 className="text-3xl font-bold text-gray-800">Service Management</h1>
        <button className="bg-blue-600 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded-lg shadow-md hover:shadow-lg transition-all duration-200 ease-in-out flex items-center">
          <PlusIcon className="h-5 w-5 mr-2" />
          Add Service
        </button>
      </div>

      <div className="bg-white rounded-xl shadow-lg p-6">
        <div className="flex items-center mb-4">
          <div className="relative w-full max-w-md">
            <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
              <MagnifyingGlassIcon className="h-5 w-5 text-gray-400" />
            </div>
            <input
              type="text"
              placeholder="Search by service name..."
              className="w-full pl-10 pr-4 py-2 border border-gray-300 rounded-lg focus:ring-blue-500 focus:border-blue-500 transition-colors"
              onChange={(e) => setSearchTerm(e.target.value)}
            />
          </div>
        </div>

        <div className="overflow-x-auto">
          <table className="min-w-full divide-y divide-gray-200">
            <thead className="bg-gray-50">
              <tr>
                {serviceColumns.map((col) => (
                  <th 
                    key={col.key} 
                    scope="col" 
                    className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider"
                    onClick={() => col.sortable && requestSort(col.key as keyof Service)}
                  >
                    <div className="flex items-center">
                      {col.label}
                      {col.sortable && (
                        <span className="ml-1">
                          {sortConfig?.key === col.key ? (
                            sortConfig.direction === 'asc' ? <ChevronUpIcon className="h-4 w-4" /> : <ChevronDownIcon className="h-4 w-4" />
                          ) : (
                            <ChevronUpIcon className="h-4 w-4 text-gray-300" />
                          )}
                        </span>
                      )}
                    </div>
                  </th>
                ))}
              </tr>
            </thead>
            <tbody className="bg-white divide-y divide-gray-200">
              {filteredAndSortedServices.map((service) => (
                <tr key={service.id} className="hover:bg-gray-50 transition-colors">
                  {serviceColumns.map(col => (
                    <td key={col.key} className="px-6 py-4 whitespace-nowrap">
                      {renderCell(service, col.key)}
                    </td>
                  ))}
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </div>
    </div>
  )
}
